@import '~@/style/var.less';
.prescriptionCount {
  .prescriptionCountItem {
    .icon {
      float: left;
      width: 55px;
      vertical-align: top;
      :global(img) {
        width: 100%;
      }
    }
    .content {
      text-align: right;
      div:first-child {
        color: @break-color3;
        font-weight: 500;
        font-size: 38px;
        line-height: 48px;
      }
      div:last-child {
        color: @break-color4;
        font-size: 14px;
        line-height: 24px;
      }
    }
  }
}
